export default "@import '../../themes/src/default.css';\n\n:root {\n    --checkbox-group-error-color: var(--color-light-text-negative);\n    --checkbox-group-hint-color: var(--color-light-text-secondary);\n    --checkbox-group-label-color: var(--color-light-text-primary);\n    --checkbox-group-error-padding-left: var(--gap-xs);\n    --checkbox-group-error-border-left: 1px solid var(--color-light-text-negative);\n}\n\n.component {\n    display: flex;\n    flex-direction: column;\n}\n\n.error {\n    padding-left: var(--checkbox-group-error-padding-left);\n    border-left: var(--checkbox-group-error-border-left);\n}\n\n.checkboxList {\n    display: flex;\n}\n\n.vertical .checkboxList {\n    flex-direction: column;\n    align-items: flex-start;\n}\n\n.vertical .checkbox {\n    margin-bottom: var(--gap-m);\n\n    &:last-child {\n        margin-bottom: 0;\n    }\n}\n\n.horizontal .checkboxList {\n    flex-wrap: wrap;\n    margin-bottom: var(--gap-xs-neg);\n}\n\n.horizontal .checkbox {\n    margin-right: var(--gap-xl);\n    margin-bottom: var(--gap-xs);\n}\n\n.horizontal .tagLabel {\n    margin-right: var(--gap-xs);\n}\n\n.horizontal .checkbox:last-child,\n.horizontal .tagLabel:last-child {\n    margin-right: 0;\n}\n\n.label {\n    @mixin paragraph_primary_medium;\n    margin-bottom: var(--gap-s);\n    color: var(--checkbox-group-label-color);\n}\n\n.sub {\n    @mixin paragraph_component_secondary;\n    margin-top: var(--gap-s);\n}\n\n.errorMessage {\n    color: var(--checkbox-group-error-color);\n}\n\n.hint {\n    color: var(--checkbox-group-hint-color);\n}\n\n.hiddenInput {\n    position: absolute;\n    z-index: -1;\n    top: 0;\n    left: 0;\n    opacity: 0;\n}\n\n.tagLabel {\n    position: relative;\n    max-width: 100%;\n}\n";